.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.btn {
  padding: 20px 28px;
  background-color: var(--accent-color);
  color: var(--light-color);
  text-align: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 140%;
  max-width: 247px;



  @include mobile {
    max-width: inherit;
    width: 100%;
  }

  &--big {
    margin-top: 60px;
    max-width: 100%;

    width: 100%;

    @include tablet {
      display: none;
    }
  }

  &--mini {
    padding: 11px 20px;
    background-color: var(--accent-color);
    color: var(--light-color);
    text-align: center;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
    width: 155px;
  }

  &--pad {
    padding: 11px 70px;
  }

  &--form {

    @include tablet {
      display: block;
      margin: 0 auto;
    }

  }
}

.section-title {
  margin: 0;
  margin-bottom: 30px;
  font-size: 48px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;

  &--white {
    color: var(--light-color);
  }

  @include desktop {
    font-size: 40px;
  }

  @include tablet {
    text-align: center;
    margin-bottom: 14px;
    font-size: 32px;
  }

  @include mobile {
    font-size: 24px;
  }
}

.section-accent {
  color: var(--accent-color);
}

.map {
  &__map {
    height: 660px;
    width: 100%;

    @include mobile {
      height: 400px;
    }
  }
}


