.form {
  padding: 104px 0;
  margin-bottom: 130px;
  position: relative;
  background: radial-gradient(50% 50% at 50% 50%, #20233A 0%, #0C0D0E 100%);

  @include tablet {
    padding-bottom: 180px;
    margin-bottom: 100px;
    padding-top: 50px;
  }

  @include mobile {
    padding-bottom: 200px;
    margin-bottom: 80px;
    padding-top: 33px;
  }

  &::after {
    content: '';
    width: 100%;
    height: 100%;
    background-image: url(../img/form-bg.webp);
    background-repeat: no-repeat;
    background-position: top left;
    top: 40px;
    position: absolute;

    @include desktop {
      background-image: url(../img/form-bg-3.webp);
    }

    @include tablet {
      background-image: url(../img/form-bg-4.webp);
      top: 405px;
    }

    @include mobile {
      background-image: url(../img/form-bg-5.webp);
      top: 384px;

    }

    @include mobile-mini {
      top: 460px;
    }
  }

  &--two {
    margin: 0;
    padding: 174px 0;


    @include tablet {
      padding-bottom: 290px;
      padding-top: 80px;
    }

    @include mobile {
      padding-bottom: 270px;
    }

    &::after {
      content: '';
      width: 100%;
      height: 100%;
      background-image: url(../img/form-bg-2.webp);
      background-repeat: no-repeat;
      background-position: top left;
      top: 6px;
      position: absolute;

      @include desktop {
        background-image: url(../img/form-bg-6.webp);
      }

      @include tablet {
        background-image: url(../img/form-bg-7.webp);
        top: 433px;
      }

      @include mobile {
        background-image: url(../img/form-bg-8.webp);
        top: 455px;
      }
    }
  }

  &__box {
    @include desktop {
      max-width: 560px;
    }
  }



  &__content {
    position: relative;
    z-index: 2;
    max-width: 700px;
    margin: 0 auto;
    margin-right: 0;

    @include desktop {
      max-width: 560px;
    }

    @include tablet {
      margin: 0 auto;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 20px;

    @include tablet {
      max-width: 400px;
      margin: 0 auto;
    }
  }

  &__input {
    max-width: 400px;
    padding: 20px;
    background: rgba(249, 249, 249, 0.10) !important;
    color: var(--light-color);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;

    &::placeholder {
      opacity: .5;
    }

  }

  &__radio {
    border-radius: 4px;
    border: 1px solid var(--Style, #D60926);
    width: 20px;
    height: 20px;
    background-color: inherit;

    &:checked {
      background-image: url(../img/check.svg);
      background-repeat: no-repeat;
      background-position: center top 4px;
    }
  }

  &__label {
    color: var(--light-color);
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;

    @include tablet {
      font-size: 14px;
    }
  }

  &__box {
    margin: 5px 0;
    display: flex;
    align-items: center;
    gap: 14px;
  }

  &__lab {
    display: flex;
    flex-direction: column-reverse;
    gap: 5px;
  }
}
