.tabs {
  &__nav {
    padding: 0;
    margin: 0 auto;
    margin-right: 0;
    max-width: 700px;
    display: flex;
    gap: 60px;

    @include tablet {
      justify-content: center;
      margin-top: 10px;
    }
  }

  &__nav-btn {
    padding-bottom: 20px;
    position: relative;
    color: rgba(#000000, .4);
    font-size: 48px;
    font-weight: 700;
    transition: all .2s ease-in-out;

    @include tablet {
      font-size: 34px;
    }

    @include mobile {
      font-size: 24px;
    }

    &--active {

      color: var(--accent-color);

      &::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 3px;
        background-color: var(--accent-color);
        bottom: 0;
      }
    }

    &:focus {
      color: var(--accent-color);
      background-color: inherit;
    }
  }

  &__box {
    display: flex;
    justify-content: space-between;


    @include tablet {
      gap: 20px;
    }

    @include mobile {
      flex-direction: column;

      gap: 10px;
    }
  }

  &__list {
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    max-width: 1200px;
    padding:0 15px ;
    margin: 0 auto;
    margin-bottom: 50px;
    margin-top: 20px;
  }

  &__text {
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;

    &:last-child {
      display: block;
      max-width: 700px;
      width: 100%;

      @include tablet {
        max-width: 450px;
      }
    }
  }

}
