.polygon {
  margin-bottom: 130px;
  @include tablet {
    margin-bottom: 100px;
  }

  @include mobile {
    margin-bottom: 80px;
  }
  &__box {
    max-width: 700px;
    margin: 0 auto;
    margin-right: 0;
  }

  &__descr {
    margin: 0;
    margin-bottom: 60px;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;

    @include tablet {
      text-align: center;
      margin-bottom: 40px;
    }
  }

  &__content {
    display: flex;
    gap: 24px;

    @include tablet {
      gap: 10px;
    }

    @include mobile {
      overflow-x: scroll;
    }

  }

  &__item {

    @include mobile {
      width: 280px;
      height: 280px;
    }
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    gap: 21px;

    @include mobile {
      flex-direction: row;

      gap: 10px;
    }


  }

  &__png {
    max-width: none;
    height: 258px;
    width: 100%;


    @include mobile {
      height: 100%;
    }

  }

  &__img {

    height: 540px;
    width: 100%;

    @include mobile {
      height: 280px;
      object-fit: fill;
      width: 280px;
      max-width: inherit;
    }
  }
}
