.gift {
  margin-bottom: 130px;

  @include tablet {
    margin-bottom: 100px;
  }

  @include mobile {
    margin-bottom: 80px;
  }

  &__content {
    display: flex;
    justify-content: space-between;

    @include tablet {
      flex-direction: column;
    }
  }

  &__box {
    margin-top: 40px;
    max-width: 700px;
  }

  &__text {
    margin: 0;
    margin-bottom: 60px;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;

    @include tablet {
      text-align: center;
      margin-bottom: 30px;
    }
  }

  &__img {
    min-width: clamp(516px, 10vw, 516px);
    margin-right: 24px;

    @include tablet {
      min-width: clamp(416px, 10vw, 516px);
    }

    @include mobile {
      min-width: clamp(290px, 10vw, 516px);
    }
  }
}
