.class {
  margin-bottom: 130px;

  @include tablet {
    margin-bottom: 100px;
  }

  @include mobile {
    margin-bottom: 80px;
  }

  &__box {
    max-width: 700px;
    margin: 0 auto;
    margin-right: 0;
  }

  &__descr {
    margin: 0;
    margin-bottom: 40px;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;

    @include tablet {
      text-align: center;
    }
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
    gap: 22px;

    @include mobile {
      overflow-x: scroll;
      flex-wrap: nowrap;
    }
  }

  &__item {
    @include mobile {
      width: 280px;
      height: 253px;
    }

  }

  &__wrapper {
    display: flex;
    gap: 22px;

    @include mobile {
      gap: 10px;
    }
  }

  &__img {
    max-width: none;
    width: 100%;

    @include mobile {
      height: 100%;
    }
  }
}
