.program {
  margin-bottom: 130px;

  @include tablet {
    margin-bottom: 100px;
  }

  @include mobile {
    margin-bottom: 80px;
  }

  &__item {
    position: relative;
    padding: 40px 0;
    transition: all .3s ease-in-out;

    &:first-child {
      padding-top: 0;

      @include tablet {
        padding-bottom: 0;
      }
    }


    &:not(:first-child) {
      &:hover {
        background-color: #F7F7F7;
      }
    }

    @include tablet {
      padding: 30px 0;
    }

  }

  &__wrapper {
    padding: 40px 0;
    margin: -40px 0;
    display: flex;
    transition: border-bottom .3s ease-in-out;
    border-bottom: 1px solid rgba(#000000, .1);

    @include tablet {
      padding: 30px 0;
        margin: -30px 0;
    }

    @include mobile {
      flex-direction: column;
    }
  }

  &__inner {
    max-width: 700px;
    width: 100%;
  }

  &__box {
    position: relative;
    max-width: 700px;
    margin: 0 auto;
    margin-right: 0;

    @include desktop {
      max-width: 560px;
    }

    @include tablet {
      margin: 0 auto;
    }


  }

  &__descr {
    margin: 0;
    margin-bottom: 17px;
    color: var(--Style, #151515);
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;

    @include tablet {
      text-align: center;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 20px;
    font-size: 24px;
    font-weight: 700;
    line-height: 120%;

    @include tablet {
      margin-bottom: 10px;
    }

    @include mobile {
      font-size: 18px;
    }
  }

  &__time {
    display: block;
    margin-bottom: 20px;
    color: var(--Style, #151515);
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;

    @include tablet {
      display: inline;
    }
  }

  &__price {
    display: block;
    margin-bottom: 20px;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;

    @include tablet {
      display: inline-block;
      margin-left: 70px;
      margin-bottom: 16px;
    }

    &--last {
      margin-left: 0;
    }

  }

  &__list {
    padding-inline-start: 28px;
    transition: all .3s ease-in-out;

    @include desktop {
      padding-inline-start: 20px;
    }

    @include tablet {
      max-height: 0px;
      visibility: hidden;
      opacity: 0;
      overflow: hidden;
      margin: 0;


    }

    &--active {
      margin-top: 16px;
      visibility: visible;
      opacity: 1;
      margin-bottom: 20px;
    }
  }

  &__list-item {

    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
  }

  &__img {
    min-width: clamp(416px, 10vw, 476px);
    padding-right: 24px;
    height: 309px;
    object-fit: contain;

    @include tablet {
      min-width: clamp(276px, 10vw, 416px);
      height: clamp(160px, 10vw, 309px);
      object-fit: fill;
      margin-top: 10px;
    }

    @include mobile {
      object-fit: fill;
      height: 300px;
      min-width: clamp(300px, 10vw, 416px);
      padding: 0;
      margin-bottom: 20px;
    }
  }

  &__tablet {
    display: none;

    @include tablet {
      display: block;
    }
  }

  &__acardion {
    position: relative;
    margin-left: 16px;
    color: var(--accent-color);
    text-align: right;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
    transition: all .3s ease-in-out;

    &::after {
      content: '';
      position: absolute;
      width: 12px;
      height: 10px;
      top: 7px;
      right: -19px;
      background-image: url(../img/acardion.svg);
      background-repeat: no-repeat;
      transform: rotate(0deg);
      transition: all .5s ease-in-out;
    }

    &--active {
      &::after {

        transform: rotate(180deg);
      }
    }
  }


}
