.cars {
  margin-bottom: 130px;

  @include tablet {
    margin-bottom: 100px;
  }

  @include mobile {
    margin-bottom: 80px;
  }

  &__box {
    max-width: 700px;
    margin: 0 auto;
    margin-right: 0;

    @include tablet {
      margin: 0 auto;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 60px;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;

    @include tablet {
      margin-bottom: 40px;
      text-align: center;
    }
  }

  &__content {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;

    @include tablet {
      gap: 10px;
    }

    @include mobile {
      flex-wrap: nowrap;
      overflow-x: scroll;
    }
  }

  &__item {
    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: 0;
    min-width: clamp(286px, 10vw, 384px);

    &:nth-child(1) {
      @include tablet {
        min-width: clamp(180px, 10vw, 384px);
        width: 100%;

        @include mobile {
          min-width: 280px;
          height: 200px;
        }
      }
    }

    &:nth-child(2) {
      @include tablet {
        min-width: clamp(180px, 10vw, 384px);
        width: 100%;

        @include mobile {
          min-width: 280px;
          height: 200px;
        }
      }
    }

    &:nth-child(3) {
      @include tablet {
        min-width: clamp(180px, 10vw, 286px);
        width: 100%;

        @include mobile {
          min-width: 280px;
          height: 200px;
        }
      }
    }

    @include mobile {
      min-width: 280px;
      height: 200px ;
    }
  }

  &__img {
    width: 100%;

    @include mobile {
      height: 100%;
    }
  }
}
