.footer {
  background-color: var(--dark-color);
  padding: 122px 0;

  @include tablet {
    padding: 80px 0;
  }

  @include mobile {
    padding: 40px 0;
  }

  &__content {
    position: relative;
  }

  &__socials {
    position: absolute;
    right: 0;
    top: 0;
    display: flex;
    gap: 61px;

    @include tablet {
      top: 320px;
      justify-content: center;
      flex-wrap: wrap;
      gap: 70px;
      row-gap: 31px;
      max-width: 340px;
      left: calc(50% - 170px);

    }
  }

  &__wrapper {
    margin-top: 43px;
    display: flex;
    gap: 205px;
    margin-bottom: 50px;

    @include tablet {
      flex-direction: column;
    }
  }

  &__logo {
    @include tablet {
      margin: 0 auto;
      text-align: center;
      display: block;
    }
  }

  &__phone {
    display: flex;
    flex-direction: column;
    color: var(--light-color);
    font-size: 18px;
    font-weight: 600;

    @include tablet {
      margin: 0 auto;
      text-align: center;
    }
  }

  &__descr {
    color: var(--light-color);
    font-size: 14px;
    font-weight: 400;
    line-height: 113.523%;
  }

  &__text {
    margin: 0;
    max-width: 290px;
    color: var(--light-color);
    font-size: 16px;
    font-weight: 400;
    line-height: 138.523%;

    &--two {
      color: var(--light-color);
      font-size: 14px;
      font-weight: 400;
      line-height: 113.523%;
      text-decoration-line: underline;
      text-align: left;
      margin: 0;

      @include tablet {
        text-align: center;
      }
    }

    @include tablet {
      text-align: center;
      margin: 0 auto;
    }
  }

  &__box {
    display: flex;
    flex-direction: column;
    gap: 14px;
  }

  &__nav {
    display: flex;
    justify-content: space-between;
    gap: 150px;

    @include tablet {
      flex-direction: column;
      gap: 20px;
      margin: 0 auto;
    }
  }

  &__item {
    @include tablet {
      text-align: center;
    }

    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__link {
    color: var(--light-color);
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;

    @include tablet {
      text-align: center;
    }
  }

  &__img {
    opacity: .3;

    @include tablet {
      opacity: 1;
      width: 36px;
      height: 36px;

    }
  }

  &__copirait {
    border-top: 1px solid rgba(#FFF, .1);
    padding-top: 28px;
    display: flex;
    justify-content: space-between;

    @include tablet {
      flex-direction: column;
      gap: 10px;
      text-align: center;
      border: none;
      padding: 0;
    }
  }

  &__cop {
    color: var(--light-color);
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    opacity: .5;
  }

  &__inner {
    display: flex;
    gap: 86px;

    @include tablet {
      flex-direction: column;
      gap: 10px;
    }
  }
}
