.faq {
  margin-bottom: 130px;

  @include tablet {
    margin-bottom: 100px;
  }

  @include mobile {
    margin-bottom: 80px;
  }

  &__item {
    padding-bottom: 40px;
    position: relative;
    overflow: hidden;
    padding-top: 30px;

    &:not(:first-child) {
      padding: 40px 0;

      @include tablet {
        padding: 76px 0;
        padding-bottom: 30px;
      }
    }

    &:first-child {
      padding-top: 76px;
    }

    @include tablet {
      padding-bottom: 30px;
    }

    border-bottom: 1px solid rgba(#000000, .1);

    &:nth-child(1) {
      &::before {
        content: '01';
        position: absolute;
        left: 0;
        top: 30px;
        font-size: 16px;
        font-weight: 400;
        line-height: 140%;

        @include tablet {
          top: 35px;
        }
      }


    }

    &:nth-child(2) {
      &::before {
        content: '02';
        position: absolute;
        left: 0;
        top: 40px;
        font-size: 16px;
        font-weight: 400;
        line-height: 140%;
      }
    }

    &:nth-child(3) {
      &::before {
        content: '03';
        position: absolute;
        left: 0;
        top: 40px;
        font-size: 16px;
        font-weight: 400;
        line-height: 140%;
      }
    }

    &:nth-child(4) {
      &::before {
        content: '04';
        position: absolute;
        left: 0;
        top: 40px;
        font-size: 16px;
        font-weight: 400;
        line-height: 140%;
      }
    }

  }

  &__wrapper {
    max-width: 700px;
    margin: 0 auto;
    margin-right: 0;
  }

  &__subtitle {
    max-width: 540px;
    margin: 0;
    font-size: 24px;
    font-weight: 700;
    line-height: 120%;
    transition: all .3s ease-in-out;

    &--active {
      margin-bottom: 20px;
    }

    @include mobile {
      font-size: 18px;
      max-width: 258px;
    }
  }

  &__text {
    max-width: 700px;
    margin: 0 auto;
    margin-right: 0;
    max-height: 0;
    visibility: hidden;
    opacity: 0;
    transition: all .3s ease-in-out;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;

    &--active {
      visibility: visible;
      opacity: 1;
    }
  }

  &__box {
    max-width: 700px;
    margin: 0 auto;
    margin-right: 0;

    @include tablet {
      margin: 0 auto;
    }
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__btn {
    width: 22px;
    height: 2px;
    background-color: #151515;
    position: relative;
    transition: all .3s ease-in-out;

    &::before {
      content: '';
      position: absolute;
      width: 22px;
      height: 2px;
      background-color: #151515;
      top: 0;
      left: 0;
      transform: rotate(90deg);
      transition: all .3s ease-in-out;
    }

    &--active {
      &::before {
        transform: rotate(0deg);
      }
    }
  }

  &__img {
    margin-bottom: 20px;
  }
}
