.graph-modal {

  &__container {
    padding: 0;
    border-radius: 0;
  }

  &__close {
    width: 50px;
    height: 50px;
    background-image: url(../img/close.svg);
    top: 0;
    right: 0;
    z-index: 5;
  }

  &__wrapper {
    position: relative;
    padding: 135px 0 147px 0;

    @include mobile {
      padding: 110px 0;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 24px;
    color: var(--dark-color);
    font-size: 24px;
    font-weight: 700;
    line-height: 120%;
    visibility: visible;
    opacity: 1;
    transition: all .3s ease-in-out;

    @include mobile {
      padding: 0 20px;
    }

    &--active {
      visibility: hidden;
      opacity: 0;
    }
  }

  &__content {
    display: flex;
    gap: 40px;
  }

  &__inner {
    padding: 65px 0px 0 30px;
    max-width: 485px;
    width: 100%;
    background-image: url(../img/modal-bg.webp);

    @include tablet {
      max-width: 300px;
    }

    @include mobile {
      display: none;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding-right: 40px;
    visibility: visible;
    opacity: 1;
    transition: all .3s ease-in-out;

    @include mobile {
      padding: 0 20px;
    }

    &--active {
      visibility: hidden;
      opacity: 1;
    }
  }

  &__form-two {
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding-right: 40px;
    visibility: visible;
    opacity: 1;
    transition: all .3s ease-in-out;

    @include mobile {
      padding: 0 20px;
    }

    &--active {
      visibility: hidden;
      opacity: 1;
    }
  }

  &__input {
    max-width: 400px;
    padding: 20px;
    background: #F7F7F7;
    color: var(--dark-color);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;

    &::placeholder {
      opacity: .5;
    }

    @include mobile {
      width: 100%;
      max-width: inherit;
    }

  }

  &__radio {
    border-radius: 4px;
    border: 1px solid var(--Style, #D60926);
    width: 20px;
    height: 20px;
    background-color: inherit;


    &:checked {
      background-image: url(../img/check.svg);
      background-repeat: no-repeat;
      background-position: center top 4px;
    }
  }

  &__label {
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
    transition: all .3s ease-in-out;

    @include tablet {
      font-size: 12px;
    }
  }

  &__box {
    margin: 5px 0;
    display: flex;
    align-items: center;
    gap: 14px;
  }

  &__lab {
    display: flex;
    flex-direction: column-reverse;
    gap: 5px;
  }

  &__title {
    display: block;
    margin: 24px 0 12px 0;
    color: var(--Style, #151515);
    font-size: 24px;
    font-weight: 700;
    line-height: 120%;
  }

  &__text {
    max-width: 330px;
    margin: 0;
    margin-bottom: 60px;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
  }

  &__complit {
    position: absolute;
    top: 150px;
    visibility: hidden;
    opacity: 0;
    transition: all .3s ease-in-out;

    &--active {
      opacity: 1;
      visibility: visible;
    }

    @include mobile {
      left: calc(50% - 170px);
    }
  }
}
