.header {

  &__content {
    padding: 29px 0 33px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include tablet {}
  }

  &__logo {
    margin-right: 40px;
    position: relative;
    z-index: 5;

    @include tablet {}
  }

  &__inner {
    position: relative;
    z-index: 5;

    &:first-child {
      margin-right: 20px;

      @include desktop {
        display: none;
      }
    }

    @include desktop {
      margin-right: 44px;
    }

    @include mobile {
      display: none;
    }
  }

  &__box {
    display: flex;
    align-items: center;
  }

  &__address {
    position: relative;
    margin: 0;
    max-width: 290px;
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
    color: var(--dark-color);
    z-index: 5;

    @include tablet {
      display: none;
    }
  }

  &__text {
    color: var(--dark-color);
    display: block;
    text-align: right;
    font-size: 14px;
    font-weight: 400;
    line-height: 113.523%;
    text-decoration-line: underline;
    margin: 0 auto;
    margin-right: 0;

    @include mobile {
      display: block;
      margin: 0;
    }
  }

  &__time {
    margin: 0;
    text-align: right;
    font-size: 18px;
    font-weight: 600;

    @include tablet {
      display: none;
    }
  }

  &__phone {
    color: var(--dark-color);
    display: block;
    text-align: right;
    font-size: 18px;
    font-weight: 600;
  }

  &__mail {
    display: block;
    text-align: right;
    color: var(--dark-color);
    font-size: 14px;
    font-weight: 400;
    line-height: 113.523%;
    text-decoration-line: underline;
  }

  &__mobile {
    position: relative;
    display: none;
    padding: 8px 20px;
    background-color: #FFF;
    z-index: 1000;

    @include mobile {
      display: flex;
      justify-content: space-between;
    }
  }
}

.burger {
  position: relative;
  @include burger;
  z-index: 111;
  display: none;

  @include desktop {
    display: block;
  }
}
