/* stylelint-disable */

@mixin desktop {
  @media (max-width: (1080px)) {
    @content;
  }
}

@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (975px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (576px)) {
    @content;
  }
}
@mixin mobile-mini {
  @media (max-width: (380px)) {
    @content;
  }
}
