.menu {

  position: absolute;
  top: 0;
  right: 0px;
  width: 100%;
  visibility: hidden;
  opacity: 0;
  height: 100vh;
  z-index: 5;
  transition: all .3s ease-in-out;
  background-color: rgba(0, 0, 0, 0.7);
  @include mobile {
    top: 35px;
  }

  &--active {
    visibility: visible;
    opacity: 1;
  }

  &__content {
    position: absolute;
    padding: 92px 20px;
    right: 0;
    width: 340px;
    height: 100vh;
    background-color: var(--light-color);
  }

  &__logo {
    position: relative;
    top: -60px;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin-bottom: 63px;
  }

  &__link {
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
  }

  &__address {
    display: block;
    margin: 0;
    margin-bottom: 22px;
    font-size: 12px;
    font-weight: 400;
    line-height: 140%;
    color: var(--dark-color);

  }

  &__time {
    margin: 0;
    font-size: 12px;
    font-weight: 400;
    line-height: 138.523%;
  }

  &__mail {
    display: block;
    margin-top: 40px;
    color: var(--dark-color);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 113.523%;
    text-decoration-line: underline;
  }


}
