.hero {
  background-image: url(../img/hero-bg.webp);
  padding-top: 300px;
  margin-top: -300px;
  padding-bottom: 430px;
  position: relative;
  margin-bottom: 130px;

  @include desktop {
    padding-bottom: 367px;
    padding-top: 300px;
  }


  &::before {
    content: '';
    background-image: url(../img/car.webp);
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
    position: absolute;
    bottom: -217px;
    padding-left: 10px;
    z-index: -1;

    @include desktop {
      background-image: url(../img/car-2.webp);
      bottom: -131px;
      right: 0;
      background-position: center;
    }

    @include mobile {
      background-image: url(../img/car-3.webp);
      background-position: center;
      bottom: -285px;
      left: 0;
    }
  }

  &__content {
    padding-top: 117px;

    @include desktop {
      padding-top: 40px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 24px;
    color: #111;
    font-size: 56px;
    font-weight: 600;
    line-height: 112.5%;

    @include small-tablet {
      font-size: 40px;
    }

    @include mobile {
      text-align: center;
    }
  }

  &__accent {
    color: var(--accent-color);
  }

  &__descr {
    margin: 0;
    margin-bottom: 58px;
    color: #111;
    font-size: 20px;
    font-weight: 400;
    line-height: 140%;

    @include small-tablet {
      font-size: 16px;
    }

    @include mobile {
      text-align: center;
    }
  }
}
