.nav {
  position: relative;
  z-index: 2;

  @include desktop {
    display: none;
  }
  &__list {
    display: flex;
  }

  &__item {
    padding: 0 38px;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      width: 2px;
      height: 2px;
      border-radius: 50%;
      left: 0;
      top: 10px;
      background-color: var(--dark-color);
    }

    &:last-child {
      &::after {
        content: '';
        position: absolute;
        width: 2px;
        height: 2px;
        border-radius: 50%;
        right:  0;
        top: 10px;
        background-color: var(--dark-color);
      }
    }
  }

  &__link {
    color: var(--dark-color);
    font-size: 16px;
    font-weight: 400;
    line-height: 140%;
  }
}
